* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: 0 10px;
  background-color: #f5f5f5;
}

.bar-container-parent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 3rem;
  position: relative;

  .sort-name {
    position: absolute;
    top: 0px;
    left: 0;
    font-size: 1.8rem;
    z-index: 100;
  }

  .bar-container {
    width: 47%;
    height: 80vh;
    display: flex;
    align-items: flex-end;
    border: 5px solid #555;
    overflow: hidden;
    transition: all 0.2s ease-in-out;

    &:nth-child(5) {
      .bar {
        background-color: rgb(245, 152, 162);

        &:hover {
          background-color: #a23;
        }
      }
    }

    .bar {
      width: 1%;
      background-color: #add8ff;
      position: relative;
      border-width: 0 1px 0 0;

      cursor: pointer;

      &.red {
        background-color: #ff4455;
      }

      &.yellow {
        background-color: #ff3;
      }

      &.green {
        background-color: green;
      }

      &.pink {
        // background-color: rgb(22, 247, 22);
        background-color: #fefe42;
      }

      &.lightblue {
        background-color: #4242fe;
      }

      &::before {
        content: attr(data-value);
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        border: 1px solid #000;
        background-color: white;
        border-radius: 10%;
        z-index: 10;
        font-size: 1.5rem;
        padding: 5px;
        display: none;
        font-family: sans-serif;
        transition: display 0.2s ease-in-out;
      }

      &:hover {
        background-color: #08a;
        border: 1px solid #333;

        &::before {
          display: block;
        }
      }
    }

    &.selected {
      border: 5px solid green;
      box-shadow: 0 0 6px rgba($color: #000000, $alpha: 0.6);
      transform: scale(1.01);

      .bar {
        border: none;
      }
    }
  }

  .start-button {
    padding: 2px 5px;
    font-size: 1rem;
    cursor: pointer;
    border: 2px solid #000;
    border-radius: 5px;
    transition: all 0.2s ease-in-out;
    margin: 0 0.5rem;

    &:hover:not(:disabled) {
      background-color: #000;
      color: #fff;
    }
  }
}

.buttons-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px 0;

  > button {
    margin: 0 10px;
    padding: 10px 20px;
    cursor: pointer;
    box-shadow: 0 0 2px rgba($color: #000000, $alpha: 0.6);
    border: 2px solid #000;
    border-radius: 5px;
  }

  > input,
  label {
    position: relative;
    margin: 0 10px;
  }

  > input::before {
    content: attr(value);
    position: absolute;
    left: 50%;
    top: -30px;
    padding: 5px;
    background-color: white;
    border: 1px solid #000;
    border-radius: 10%;
  }
}

.result {
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000;
  min-height: 100vh;
  padding: 0 2rem;

  .result-info {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 2rem;
    margin-top: 4rem;
    #restart {
      margin: 20px 0;
      padding: 15px 30px;
      cursor: pointer;
      font-size: 1.5rem;
      border: 2px solid #000;
      transition: all 0.2s ease-in-out;

      &:hover {
        background-color: #000;
        color: #fff;
      }
    }

    .sort-info {
      margin: 1rem 0;

      > h1 {
        font-size: 2rem;
      }

      ul {
        margin: 1rem 0;
        li {
          font-size: 1.5rem;
          color: #555;
          margin: 1rem 0;
        }
      }
    }
  }

  .chart {
    width: 600px;
    height: 600px;
    margin: 0 2rem;
  }
}
